<template>
  <div class="discount">
    <p>지금 결제하면 {{ amount }}% 할인</p>
  </div>
</template>
<script>
export default {
  name: 'Discount',
  data() {
    return {
      amount: 30
    }
  },
  mounted() {
    var amountInterval = setInterval(() => {
      if(this.amount > 0) {
        this.amount -= 1
      } else {
        clearInterval(amountInterval);
      }
    },1000);
  }
}
</script>
