<template>
  <div class="black-bg" v-if="modalState == true">
    <div class="white-bg">
      <h4>{{oneroomInf[modalIdx].title}}</h4>
      <p>{{oneroomInf[modalIdx].content}}</p>
      <!-- @input="month = $event.target.value" -->
      <input v-model.number="month" type="text">
      <p>{{ month }}개월 선택함 : {{ month * oneroomInf[modalIdx].price}}</p>
      <button type="button" @click="$emit('modalClose')">닫기</button>
    </div> c
  </div>
</template>
<script>
export default {
  name: 'Modal',
  props: {
    oneroomInf: Array,
    modalIdx: Number,
    modalState: Boolean,
  },
  data() {
    return {
      month: 1,
    }
  },
  watch : { //data input 요소 등 사용자가 입력하는 화면 감시 함수
    //month Data 감시
    month(monthWatch) {
      if(monthWatch >= 13) {
        alert('숫자 13이상 입력하지 마세요.');
        this.month = 1;
      } else if(isNaN(monthWatch) == true) {
        alert('숫자를 입력해 주세요.')
        this.month = 1;
      }
      /* if(monthWatch <= 2) {
        alert('2개월 이하는 원룸 월세를 팔지 않습니다.');
      } */
    }
  }
}
</script>
